@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
    list-style-type: none;
}

.youtube {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Roboto', sans-serif;
}
.youtube-videos{
    padding-top: 25px;
    max-width: 1200px;
    margin: 0 auto;
}

.youtube-videos-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;

}

.youtube-videos-list__item {
    flex: 0 0 210px;
    margin: 8px 3px;

}

.youtube-videos-list__img{
    max-width: 210px;
    height: 118px;
}

.youtube-videos-list__text{
    font-size: 14px;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 210px;
    font-weight: 500;
    height: 3.2rem;
    line-height: 1.6rem;
    cursor: pointer;
    margin-top: 2px;
}
.youtube-search {
    background: #2C2C2C;
    min-height: 56px;
    text-align: center;
    line-height: 52px;
    width: 100%;
}

.youtube-search__btn {
    background: #3D3D3D;
    color: #777777;
    border: 1px solid #3D3D3D;
    line-height: normal;
    padding: 6px;
    font-size: 16px;
    width: 60px;
    outline: none;
    cursor: pointer;
}

.youtube-search__input {
    background: #000000;
    padding: 6px;
    color: #707070;
    font-size: 16px;
    font-weight: 400;
    border: 1px solid #000000;
    outline: none;
}

.youtube-search__input[type="text"]:focus {
    border: 1px solid #707070
}

.youtube-search__btn[type="button"]:focus {
    border: 1px solid #707070
}

input[type="submit"], input[type="text"] {
    line-height: normal !important;
}
.youtube-moreVideos {
    display: block;
    background: #3D3D3D;
    color: #777777;
    border: 1px solid #3D3D3D;
    line-height: normal;
    padding: 6px;
    font-size: 16px;
    width: 160px;
    outline: none;
    cursor: pointer;
    margin-bottom: 20px;
}
